















import { Component, Prop, Emit, Vue } from 'vue-property-decorator';
import apiUser from '../api/user';

@Component({})
export default class UploadImg extends Vue {

    /** 上传的图片路径 */
    @Prop({
        type: String,
        default: ''
    })
    private imgUrl!: string;

    /** 图片宽度 */
    @Prop({
        type: Number,
        default: 240
    })
    width!: number;

    /** 加载动画 */
    private loading = false;

    /** 发送数据到父组件中 */
    @Emit('getImgUrl') sendImgSrc(src: string) {}

    /** 上传图片 */
    private uploadImg() {
        // 这样写也可以，但是命令台会报错，浏览器不会
        // const input = (this.$refs.uploadinput as HTMLInputElement);

        const input: any = this.$refs.uploadinput;

        const file: File = input.files[0];
        // console.log('上传图片文件 >>', file);
        
        /** 上传类型数组 */
        let typeStr = ['image/jpg', 'image/png', 'image/jpeg', 'image/gif'];

        // 判断文件类型
        if (typeStr.indexOf(file.type) < 0) {
            this.$message.warning('文件格式只支持：jpg 和 png');
            return;
        }

        // 判断大小
        if (file.size > 2 * 1024 * 1024) {
            this.$message.warning('上传的文件不能大于2M');
            return;
        }

        // const formData = new FormData();

        // formData.append('file', file);

        this.loading = true;
        apiUser.uploadImg(file, res => {
            this.loading = false;
            /** 图片路径 */
            let src: string = res;
            this.sendImgSrc(src);
        }, err => {
            this.loading = false;
            this.$message.error('上传图片失败');
            // console.log('上传图片失败 >>', err);
        });
    }

    /** 清除当前图片 */
    private removeImg() {
        this.sendImgSrc('');
    }
}
